* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;  
}

@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Great+Vibes&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

body {
  background: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100%;
  height: 100vh;
  overflow: hidden;
}

.main-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.timer-heading {
  font-family: 'Great Vibes', Avenir, sans-serif;
  font-size: 4rem;
  font-weight: 400;
}

.timer-number {
  font-family: 'Bebas Neue';
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  background: #111;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #222;
  border-radius: .25rem;
  margin-right: 1rem;
}

.timer-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Raleway';
}

.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
  position: relative;
}

.dumpling-hands{
  width: 4rem;
  height: auto;
  position: absolute;
  top: -4.75px;
  z-index: 999;
}

.dumpling-img {
  width:7rem;
  height: auto;
}

.motion-button {
  font-family: "Raleway";
  font-size: 1.25rem;
}

@media screen and (max-width: 768px) {
  .timer-heading {
    font-size: 3.5rem;
    margin-bottom: 2rem;
  }
  .timer-container {
    display: block;
    margin: 10px auto;
  }
}